@import "../variables.scss";

.modeSelector {
    align-self: flex-start;
    display: flex;

    @media screen and (max-width: 1140px) {
        margin-left: $gap;
    }

    & > div:nth-child(3) {
        @media screen and (max-width: 540px) {
            display: none;
        }   
    }
}