@import "./variables.scss";

.App {

    & > h1 {
        text-align: center;
    }
}

.container {
    display: flex;
    justify-content: center;
}

.chartContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.vowels {
    display: flex;
    justify-content: space-between;
    width: 518px;
    border: $container-border solid transparent;
    border-radius: $border-radius;
    padding: $padding;
    margin-bottom: $padding;

    @media screen and (max-width: 540px) {
        padding: calc($padding * $factor);
        width: 363.333px;
    }

    &.vowelsLine {
        border-color: $vowels;
    }
}

.keyboardContainer {
    position: absolute;
    top: 0;
    right: calc($container-margin - $keyboard-container-width);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-self: flex-start;
    width: $keyboard-container-width;

    @media screen and (max-width: 1140px) {
        position: static;
        flex-direction: row;
        width: 100%;
        align-self: center;
    }
}

.description {
    border: 1px solid black;
    height: calc($button * 2);
    padding: 0 $button;
    border-radius: $button;
    position: absolute;
    background-color: antiquewhite;
    // opacity: 0.9;
    z-index: 999;
}